import React from 'react';
import { FaListUl } from 'react-icons/fa'
import { AiFillSetting } from 'react-icons/ai'
import './HeaderMobile.scss'
import Profile from '../Profile';

function HeaderMobile(props) {
    return (
        <div className="header-mobile flex">
            <button className="btn-list">
                <span><FaListUl /></span>
            </button>
            <a href="/" className="home">
                <img src={process.env.PUBLIC_URL + '/images/avatar/cry.png'} alt="" />
                <span>MP5 Zing</span>
            </a>
            <div className="setting">
                <label className="btn-setting" htmlFor="openMenu-setting">
                    <span><AiFillSetting /></span>
                </label>
                <input type="checkbox" id="openMenu-setting" />
                <div className="profile-mobile">
                    <Profile />
                </div>
            </div>

        </div>
    );
}

export default HeaderMobile;